import * as React from 'react';
import { School } from "kedbackend/models";
import { CourseBanner } from "../courses/course-banner";
import { showError, L } from "../../../utils/utils";
import env from '../../../globals/KED.env';
import { createUUID, DocumentAccess, BatchRunner } from "kedbackend/client";
import { Link } from 'react-router-dom';

interface Props {
  id?: string
  title: string
  feedbackUrl: string
}

type State = School;

export class EditSchool extends React.Component<Props,State> {
  origSchool: School;

  constructor(props: Props) {
    super(props);
    this.state = null;
  }

  componentDidMount() {
    this.load().catch(err => showError(err));
  }

  async load() {
    if (this.props.id) {
      // ID given, load it.
      const { id } = this.props;
      const school = await env.kedBackendClient.get<School>("schools", id);
      this.origSchool = { ...school };
      this.setState({ ...school });
    } else {
      // No id, create new instead.
      this.setState({
        id: createUUID()
      });
    }
  }

  async save() {
    const r = new BatchRunner();
    let school = { ...this.state };
    if (!this.origSchool) {
      school.acl = ["role:USER:R"];
      school.acl.push(
        new DocumentAccess(
          "schoolRole",
          `${school.name}/EMPLOYEE`, "W").toString());
      r.add("schools", school);
    } else if (this.origSchool.name !== this.state.name) {

      // Unshare the old name
      r.unshare("schools", school.id, [
        new DocumentAccess("schoolRole", `${this.origSchool.name}/EMPLOYEE`, "W")
      ].map(a => a.toString()));
      // BUG workaround - renaming a school to a new name where just casing matters:
      if (this.origSchool.name.toLowerCase() === this.state.name.toLowerCase()) {
        // Workaround for bug with renaming when it differs just by casing:
        await env.kedBackendClient.batch(r.mutationRequests);
        r.mutationRequests = [];
      }
      // Share the new name
      r.share("schools", this.state.id, [
        new DocumentAccess("schoolRole", `${this.state.name}/EMPLOYEE`, "W")
      ].map(a => a.toString()));
      r.put("schools", school);
    } else {
      r.put("schools", school);
    }

    await env.kedBackendClient.batch(r.mutationRequests);

    location.hash = "#/schools";
  }

  async deleteSchool() {
    if (confirm(L`Är du säker på att ta bort skolan "${this.state.name}"`)) {
      await env.kedBackendClient.delete("schools", this.state.id);
      location.hash= "#/schools";
    }
  }

  render() {
    const { title, id } = this.props;
    const editExisting = !!id;
    const school = this.state;
    const origSchool = this.origSchool;
    const isAdmin = env.currentUser.roles.some(role => role === "ADMIN");
    return <div style={{ outline: 0 }}>
      <CourseBanner
        title="Skolor" activePage="schools" callbacks={{schools: ()=>location.hash="#/schools"}}
        routes={{feedback: this.props.feedbackUrl}} />
      <div className="sv-row sv-layout">
        <div className="pagecontent sv-layout sv-spacer-20pxvt sv-skip-spacer">
          <h2>{title}</h2>
          <hr />
          <div className="align-horizontal">
            <div className="horizontalItem top">Namn:</div>
            <div className="horizontalItem top">
              <input type="text" autoFocus
                disabled={!isAdmin}
                tabIndex={1}
                size={50}
                value={school ? school.name : ""}
                onChange={ev => this.setState({ name: ev.target.value })} />
            </div>
            <div className="horizontalItem top spaced"></div>
          </div>
          {isAdmin && editExisting && <div tabIndex={2} className="btn btn-warning btn-large pull-right" onClick={() =>
            this.deleteSchool().catch(showError)}>
            Ta bort skola
          </div>}
          <div className="pull-right">&nbsp;</div>
          <Link className="btn btn-warning btn-large pull-right" to="/schools">
            Avbryt
          </Link>
          
          {isAdmin && <a tabIndex={1} className={"btn btn-large"} onClick={() => {
            if (origSchool && origSchool.name !== school.name) {
              if (!confirm(L`Är du säker på att du vill döpa om skolan? Detta påverkar portalens funktion för lärare och elever som tillhör skolan. Namnet måste stämma exakt överens med namngivningen av skolan på användarobjekten.

Det som händer annars är att elever och lärare på skolan inte längre hittar några kurser.

Blir det fel kan du dock alltid bara döpa tillbaka skolans namn.`)) {
                return;
              }
            }
            this.save().catch(showError);
          }}>Spara</a>}
        </div>
      </div>
    </div>;
  }
}