import '../../global-setters/set-bearer-providers';
const bearerPromise = env.bearerProvider.getBearer(); // Start getting bearer as soon as possible.
import '../../global-setters/set-ked-backend-client';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import env from '../../globals/KED.env';
import { CourseBuilder } from '../../components/course-builder';
import $ from 'jquery';
import { showError, showInfo } from "../../utils/utils";
import { Spinner } from "../../components/course-builder/sub-components/spinner";

interface Props {
  viewCourseUrl: string;
  feedbackUrl: string;
}

interface State {
  isTokenLoaded: boolean;
}

export class KEDAppCourseBuilder extends React.Component<Props,State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isTokenLoaded: false
    }
  }

  componentWillMount() {
    bearerPromise.then(()=>{
      this.setState({isTokenLoaded: true});
    }).catch(err => {
      showError(err);
    });
  }

  render() {
    return this.state.isTokenLoaded ?
      <CourseBuilder
        viewCourseUrl={this.props.viewCourseUrl}
        feedbackUrl={this.props.feedbackUrl} /> :
      <p><Spinner/>V.g. vänta... autenticerar...</p>
  }
}
